import validator from 'validator'
import moment from 'moment'


const validate = (newAttendee) => {
  if(validator.equals(newAttendee.firstName, '')) {
    throw new Error('First name missing')
  }

  if(validator.equals(newAttendee.lastName, '')) {
    throw new Error('Last name missing')
  }

  if(!validator.isEmail(newAttendee.email)) {
    throw new Error('Invalid email')
  }

  if(!validator.isMobilePhone(newAttendee.phoneNumber)) {
    throw new Error('Invalid phone number')
  }

  if(!moment(newAttendee.dateOfBirth, 'DD/MM/YYYY', false).isValid()) {
    throw new Error('Invalid date')
  }
}

export default validate