import { useState } from 'react'
import { TextField, Button, Checkbox, FormControl, FormLabel, FormGroup, FormControlLabel, Stack, Box, Typography } from '@mui/material'
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined'
import FavoriteIcon from '@mui/icons-material/Favorite'
import formServices from '../services/forms'
import { useNotificationDispatch } from '../NotificationContext'
import validate from '../utils/validation'

//import { useTheme } from '@mui/material/styles'

const RegisterForm = () => {
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [phoneNumber, setPhoneNumber] = useState('')
  const [email, setEmail] = useState('')
  const [dateOfBirth, setDateOfBirth] = useState('')
  const [diet, setDiet] = useState(['noRes'])
  const [other, setOther] = useState('')

  const dispatchNotification = useNotificationDispatch()

  //const theme = useTheme()

  const inputLabelColor = {
    sx : {
      color: '#ffb3ff', '&.Mui-focused': {
        color: '#ffb3ff'
      } } }

  const iconBorder = {
    fill: '#ffb3ff'
  }

  const handleSubmit =  async (event) => {
    event.preventDefault()

    const newAttendee = {
      firstName,
      lastName,
      phoneNumber,
      email,
      dateOfBirth,
      dietaryRestrictions: diet,
      other
    }

    try {
      validate(newAttendee)
      await formServices.createAttendee(newAttendee)
      setFirstName('')
      setLastName('')
      setPhoneNumber('')
      setEmail('')
      setDateOfBirth('')
      dispatchNotification({
        type: 'DISPLAY',
        payload: {
          notification: 'You have been registered to the event',
          errorOn: false
        }
      })
      clearNotification()
    } catch(exception) {
      dispatchNotification({
        type: 'DISPLAY',
        payload: {
          notification: `${exception.message}`,
          errorOn: true
        }
      })
      clearNotification()
    }
  }

  const handleDietChange = (event) => {
    const index = diet.indexOf(event.target.value)
    if(event.target.value === 'noRes' && index === -1) {
      setDiet([ event.target.value ])
    } else if(index === -1) {
      const noResFiltered = diet.filter(diet => diet !== 'noRes')
      setDiet([ ...noResFiltered, event.target.value ])
    } else {
      setDiet(diet.filter(diet => diet !== event.target.value))
    }
  }

  const clearNotification = () => {
    setTimeout(() => {
      dispatchNotification({ type: 'CLEAR' })
    }, 4000)
  }

  return(
    <div>
      <Typography variant='h1' sx={{ marginTop: 'clamp(4px, 2vh, 4vh)', marginBottom: 'clamp(4px, 2vh, 4vh)' }}>Register to Jessica&apos;s Event</Typography>
      <form>
        <Stack spacing={2} direction="row" sx={{ marginBottom: 4 }}>
          <TextField
            type="text"
            variant='outlined'
            label="First Name"
            onChange={e => setFirstName(e.target.value)}
            value={firstName}
            fullWidth
            required
            InputLabelProps={inputLabelColor}
          />
          <TextField
            type="text"
            variant='outlined'
            label="Last Name"
            onChange={e => setLastName(e.target.value)}
            value={lastName}
            fullWidth
            required
            InputLabelProps={inputLabelColor}
          />
        </Stack>
        <TextField
          type="email"
          variant='outlined'
          label="Email"
          onChange={e => setEmail(e.target.value)}
          value={email}
          fullWidth
          required
          sx={{ mb: 4 }}
          InputLabelProps={inputLabelColor}
        />
        <TextField
          type="text"
          variant='outlined'
          label="Phone Number"
          onChange={e => setPhoneNumber(e.target.value)}
          value={phoneNumber}
          required
          fullWidth
          sx={{ mb: 4 }}
          InputLabelProps={inputLabelColor}
        />
        <TextField
          type="text"
          variant='outlined'
          label="Date of Birth"
          placeholder="Please type date in dd/mm/yyyy format"
          onChange={e => setDateOfBirth(e.target.value)}
          value={dateOfBirth}
          fullWidth
          sx={{ mb: 4 }}
          InputLabelProps={inputLabelColor}
        />
      </form>
      <Box sx={{ paddingBottom: '30px' }}>
        <FormControl required variant='standard'>
          <FormLabel sx={{ paddingBottom: '10px', color: '#ffb3ff' }}>Dietary Restrictions</FormLabel>
          <FormGroup>
            <FormControlLabel
              control={
                <Checkbox
                  icon={<FavoriteBorderOutlinedIcon sx={iconBorder} />}
                  checkedIcon={<FavoriteIcon />}
                  size='medium'
                  color='pink2'
                  value='noRes'
                  checked={diet.includes('noRes')}
                  onChange={handleDietChange}
                  name='no restrictions'
                />}
              label="no restrictions"
            />
            <FormControlLabel
              control={
                <Checkbox
                  icon={<FavoriteBorderOutlinedIcon sx={iconBorder} />}
                  checkedIcon={<FavoriteIcon />}
                  size='medium'
                  color='pink2'
                  value='vegan'
                  checked={diet.includes('vegan')}
                  onChange={handleDietChange}
                  name='vegan'
                />}
              label="vegan"
            />
            <FormControlLabel
              control={
                <Checkbox
                  icon={<FavoriteBorderOutlinedIcon sx={iconBorder} />}
                  checkedIcon={<FavoriteIcon />}
                  size='medium'
                  color='pink2'
                  value='vegetarian'
                  checked={diet.includes('vegetarian')}
                  onChange={handleDietChange}
                  name='vegetarian'
                />}
              label="vegetarian"
            />
            <FormControlLabel
              control={
                <Checkbox
                  icon={<FavoriteBorderOutlinedIcon sx={iconBorder} />}
                  checkedIcon={<FavoriteIcon />}
                  size='medium'
                  color='pink2'
                  checked={diet.includes('gluten')}
                  value='gluten'
                  onChange={handleDietChange}
                  name='gluten free'
                />}
              label="gluten free"
            />
            <FormControlLabel
              control={
                <Checkbox
                  icon={<FavoriteBorderOutlinedIcon sx={iconBorder} />}
                  checkedIcon={<FavoriteIcon />}
                  size='medium'
                  color='pink2'
                  value='other'
                  checked={diet.includes('other')}
                  onChange={handleDietChange}
                  name='other'
                />}
              label="other"
            />
          </FormGroup>
        </FormControl>
        {diet.includes('other') &&
        <TextField
          type="text"
          variant='outlined'
          placeholder='write your other dietary restrictions'
          onChange={e => setOther(e.target.value)}
          value={other}
          fullWidth
          sx={{ mb: 4 }}
        />
        }
      </Box>
      <Button onClick={handleSubmit} variant="outlined" color='pink' type="submit">Register</Button>
    </div>
  )
}

export default RegisterForm

